.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1040;
  transition: 0.4s ease-in-out;
}

.modal {
  background: #ffffff;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 10px 20px;
  width: 90%;
  max-width: 600px;
  padding: 2rem;
  transition: 0.2s ease-in-out;
}
.modal--sm {
  max-width: 400px;
}
.modal--md {
  max-width: 600px;
}
.modal--lg {
  max-width: 800px;
}

.modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #d8dfe7;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

.modal__title {
  font-size: 1.75rem;
  font-weight: 600;
  color: #313b5e;
}

.modal__close {
  background: none;
  border: none;
  font-size: 1.75rem;
  cursor: pointer;
  color: #6c757d;
  transition: 0.2s ease-in-out;
}
.modal__close:hover {
  color: #343a40;
}

.modal__content {
  font-size: 1rem;
  color: #6e708c;
